import store from '@/store'
import config from '@/config'
class WebSocketClient {
  constructor (confg = {}) {
    const defautConfig = {
      url: config.wsconfig.url,
      port: config.wsconfig.port,
      protocol: 'ws',
      timeInterval: 5 * 1000
    }
    const finalConfig = { ...defautConfig, ...confg }
    this.ws = {}
    this.port = finalConfig.port
    this.url = finalConfig.url
    this.protocol = finalConfig.protocol
    this.handle = null
    this.timeInterval = finalConfig.timeInterval
  }

  init () {
    this.ws = new WebSocket(`${this.protocol}://${this.url}:${this.port}`)
    this.ws.onopen = () => this.onOpen()
    this.ws.onmessage = (msg) => this.onMessage(msg)
    this.ws.onclose = () => this.onClose()
    this.ws.onerror = () => this.onError()
  }

  send (msg) {
    this.ws.send(msg)
  }

  onOpen () {
    this.send(JSON.stringify({
      event: 'auth',
      message: 'Bearer ' + store.state.token
    }))
  }

  onMessage (event) {
    if (this.isShow) {
      return
    }
    // 
    var obj = JSON.parse(event.data)
    switch (obj.event) {
      case 'noauth':
        // 
        //  /login token
        break
      case 'heartbeat':
        this.checkServer() // timeInterval + t
        // 
        this.ws.send(JSON.stringify({
          event: 'heartbeat',
          message: 'pong'
        }))
        break
      default:
        store.dispatch(obj.event, obj) // 
    }
  }

  onClose () {
    // close
    this.ws.close()
  }

  onError () {
    // error
    // 1s
    setTimeout(() => {
      this.init()
    }, 500)
  }

  checkServer () {
    clearTimeout(this.handle)
    this.handle = setTimeout(() => {
      this.onClose()
      this.onError()
      // 1ms
    }, this.timeInterval + 1000)
  }
}

export default WebSocketClient
