<template>
  <div class="my-info">
    <ValidationObserver ref="observer" v-slot="{ validate }">
      <form class="ui form" method="post">
        <ValidationProvider name="username" rules="required|email" v-slot="{ errors }">
          <div class="inline field">
            <label></label>
            <input type="text" v-model="username" placeholder="" />
            <span class="error-message">{{ errors[0] }}</span>
          </div>
        </ValidationProvider>

        <ValidationProvider name="name" rules="required" v-slot="{ errors }">
          <div class="inline field extra-field">
            <label class="info-title"></label>
            <input type="text" v-model="name" placeholder="" />
            <span class="error-message">{{ errors[0] }}</span>
          </div>
        </ValidationProvider>

        <div class="inline field extra-field">
          <label></label>
          <input type="text" v-model="location" placeholder="" />
        </div>

        <div class="inline field extra-field">
          <label class="gender"></label>
          <div class="ui radio checkbox">
            <input type="radio" v-model="gender" value="man" />
            <label></label>
          </div>
          <div class="ui radio checkbox woman-checkbox">
            <input type="radio" v-model="gender" value="woman" />
            <label></label>
          </div>
        </div>

        <div class="inline field extra-field">
          <label></label>
          <textarea v-model="regmark" rows="4" placeholder=""></textarea>
        </div>

        <button type="button" class="ui green button" @click="validate().then(submit)"></button>
      </form>
    </ValidationObserver>
  </div>
</template>
<script>
import { updateUserInfo } from '@/api/user'
import { ValidationObserver, ValidationProvider } from 'vee-validate'
export default {
  name: 'myInfo',
  components: {
    ValidationObserver,
    ValidationProvider
  },
  data () {
    return {
      username: '',
      name: '',
      location: '',
      gender: '',
      regmark: ''
    }
  },
  mounted () {
    const {
      username,
      name,
      location,
      gender,
      regmark
    } = this.$store.state.userInfo
    console.log(
      'mounted -> this.$store.state.userInfo',
      this.$store.state.userInfo
    )
    this.username = username || ''
    this.name = name || ''
    this.location = location || ''
    this.gender = gender || 'man'
    this.regmark = regmark || ''
  },
  methods: {
    async submit () {
      const isValid = await this.$refs.observer.validate()
      if (!isValid) {
        return
      }
      updateUserInfo({
        username: this.username,
        name: this.name,
        location: this.location,
        gender: this.gender,
        regmark: this.regmark
      }).then(res => {
        if (res.code === 200) {
          this.$store.commit('setUserInfo', {
            ...this.$store.state.userInfo,
            ...{
              username: this.username,
              name: this.name,
              location: this.location,
              gender: this.gender,
              regmark: this.regmark
            }
          })
          this.$alert('')
        }
      })
    }
  }
}
</script>
<style scoped>
.my-info {
  position: relative;
  top: 10px;
}
.inline.field {
  position: relative;
  padding: 5px;
}
.extra-field {
  left: 12px;
}
.error-message {
  position: relative;
  top: 2px;
  left: 10px;
  color: red;
}
.woman-checkbox {
  position: relative;
  left: 50px;
}
.ui.form textarea {
  position: absolute;
  width: 80%;
}
.ui.green.button {
  position: relative;
  top: 100px;
  left: 50px;
}
</style>
