import Vue from 'vue'
import axios from 'axios'

import App from './App.vue'
import router from './router' //  index.js 
import store from './store'

import '@/util/veevalidate'
import filters from '@/util/filter'

import Alert from './components/modules/alert'
import Pop from './components/modules/pop'

Vue.use(Alert) // 
Vue.use(Pop)

Object.keys(filters).forEach((key) => {
  Vue.filter(key, filters[key])
})

Vue.config.productionTip = false //  vue 

axios.defaults.baseURL = process.env.NODE_ENV !== 'production' ? 'http://localhost:3000' : '47.99.202.255:3000' //  node 

new Vue({
  router,
  store,
  render: h => h(App) // render: h: createElement,  html dom 
}).$mount('#app') //  app 
