<template>
  <div class="myPost">
    <table class="ui selectable celled table">
      <thead>
        <tr>
          <th></th>
          <th></th>
          <th></th>
          <th></th>
          <th></th>
          <th></th>
        </tr>
      </thead>
      <tbody>
        <tr v-for="(post,index) in list" :key="'post' +index">
          <td>
            <router-link :to="{name:'detail',params:{tid:post._id}}">{{post.title}}</router-link>
          </td>
          <td>{{post.status=="0"?"":""}}</td>
          <td>{{post.isEnd=="0"?"":""}}</td>
          <td>{{post.created|dateFormat}}</td>
          <td>{{post.reads}}/{{post.answer}}</td>
          <td>
            <router-link :to="{name:'edit',params:{tid:post._id,page:post}}"></router-link>
            <a @click.prevent="deletePost(post)"></a>
          </td>
        </tr>
      </tbody>
    </table>
  </div>
</template>
<script>
import { getPostListByUid, deletePostByUid } from '../../../api/user'
import moment from 'moment'
export default {
  name: 'myPost',
  data () {
    return {
      page: 0,
      limit: 10,
      list: {}
    }
  },
  mounted () {
    this.getPostList()
  },
  methods: {
    getPostList () {
      getPostListByUid({ page: this.page, limit: this.limit }).then(res => {
        if (res.code === 200) {
          this.list = res.data
        }
      })
    },
    // const tid=0,
    deletePost (post) {
      this.$confirm(
        '',
        () => {
          if (post.isEnd === '0') {
            deletePostByUid({ tid: post._id })
              .then(res => {
                if (res.code === 200) {
                  this.getPostList()
                }
              })
              .catch(err => {
                this.$alert(err)
              })
          } else {
            this.$alert('')
          }
        },
        () => {}
      )
    }
  },
  filters: {
    dateFormat: function (value) {
      return moment(value, 'YYYYMMDD').fromNow()
    }
  }
}
</script>
<style scoped>
.myPost {
  position: relative;
}
</style>
