<template>
  <div class="password-wrapper">
    <ValidationObserver ref="observer" v-slot="{ validate }">
      <form class="ui form" method="post">
        <ValidationProvider name="password" rules="required|max:16|min:6" v-slot="{ errors }">
          <div class="field">
            <label></label>
            <input type="password" v-model="oldPassword" placeholder="" />
          </div>
          <div class="error-message">
            <p>{{ errors[0] }}</p>
          </div>
        </ValidationProvider>

        <ValidationProvider
          name="password"
          rules="required|max:16|min:6|confirmed:confirmation"
          v-slot="{ errors }"
        >
          <div class="field">
            <label></label>
            <input type="password" v-model="newPassword" placeholder="" />
          </div>
          <div class="error-message">
            <p>{{ errors[0] }}</p>
          </div>
        </ValidationProvider>

        <ValidationProvider
          name="repassword"
          vid="confirmation"
          rules="required"
          v-slot="{ errors }"
        >
          <div class="field">
            <label></label>
            <input type="password" v-model="passwordConfirmation" placeholder="" />
          </div>
          <div class="error-message">
            <p>{{ errors[0] }}</p>
          </div>
        </ValidationProvider>

        <button type="button" class="ui green button" @click="validate().then(submit)"></button>
      </form>
    </ValidationObserver>
  </div>
</template>
<script>
import { changePassword } from '@/api/user'
import { ValidationProvider, ValidationObserver } from 'vee-validate'

export default {
  name: 'changePassword',
  components: {
    ValidationProvider,
    ValidationObserver
  },
  data () {
    return {
      oldPassword: '',
      newPassword: '',
      passwordConfirmation: ''
    }
  },
  mounted () {},
  methods: {
    async submit () {
      const isValid = await this.$refs.observer.validate()
      if (!isValid) {
        return
      }
      console.log('submit->userInfo', this.$store.state.userInfo)
      changePassword({
        oldPassword: this.oldPassword,
        newPassword: this.newPassword
      }).then(res => {
        if (res.code === 200) {
          this.username = ''
          this.name = ''
          this.password = ''
          this.repassword = ''
          this.code = ''
          requestAnimationFrame(() => {
            this.$refs.observer.reset()
          })
          this.$alert('')
        } else if (res.code === 501) {
          this.$alert('')
        }
      })
    }
  },
  created () {}
}
</script>

<style scoped>
.password-wrapper {
  position: relative;
  top: 3px;
  width: 100%;
  height: 100%;
}
.field {
  position: relative;
  top: 40px;
  left: 40px;
  width: 300px;
}
.error-message {
  position: absolute;
  left: 380px;
  color: red;
}
.ui.green.button {
  position: relative;
  top: 60px;
  left: 40px;
}
</style>
