import { extend, localize } from 'vee-validate'
import { email, required, min, max, length, confirmed, regex, is_not } from 'vee-validate/dist/rules'
import zh from 'vee-validate/dist/locale/zh_CN.json'

extend('required', required)
extend('email', email)
extend('min', min)
extend('max', max)
extend('length', length)
extend('confirmed', confirmed)
extend('regex', regex)
extend('is_not', is_not)

localize('zh_CN', {
  messages: {
    ...zh.messages,
    required: '{_filed_}'
  },
  names: {
    username: '',
    password: '',
    repassword: '',
    name: '',
    code: '',
    catalog: '',
    title: ''
  },
  fields: {
    username: {
      email: '{_field_}',
      required: '{_field_}!!!'
    },
    name: {
      required: '{_field_}!!!',
      min: '{_field_}4'
    },
    password: {
      required: '{_field_}!!!',
      min: '{_field_}6',
      max: '{_field_}16',
      confirmed: '{_field_}'
    },
    repassword: {
      required: '{_field_}!!!'
    },
    code: {
      required: '{_field_}!!!',
      length: '{_field_}4'
    },
    catalog: {
      is_not: ''
    },
    title: {
      required: '{_field_}!!!'
    }
  }
})
